namespace $ {
	export class $hyoo_life extends $mol_page {
		
		/**
		 * ```tree
		 * title @ \Life of {population} cells
		 * ```
		 */
		title() {
			return this.$.$mol_locale.text( '$hyoo_life_title' )
		}
		
		/**
		 * ```tree
		 * plugins / <= Theme
		 * ```
		 */
		plugins() {
			return [
				this.Theme()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * tools /
		 * 	<= Source_link
		 * 	<= Store_link
		 * 	<= Time
		 * ```
		 */
		tools() {
			return [
				this.Source_link(),
				this.Store_link(),
				this.Time()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Head
		 * 	<= Map
		 * ```
		 */
		sub() {
			return [
				this.Head(),
				this.Map()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Theme $mol_theme_auto
		 * ```
		 */
		@ $mol_mem
		Theme() {
			const obj = new this.$.$mol_theme_auto()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Source_link $mol_link_source uri \https://github.com/hyoo-ru/life.hyoo.ru
		 * ```
		 */
		@ $mol_mem
		Source_link() {
			const obj = new this.$.$mol_link_source()
			
			obj.uri = () => "https://github.com/hyoo-ru/life.hyoo.ru"
			
			return obj
		}
		
		/**
		 * ```tree
		 * store_link?val \
		 * ```
		 */
		@ $mol_mem
		store_link(val?: any) {
			if ( val !== undefined ) return val as never
			return ""
		}
		
		/**
		 * ```tree
		 * store_link_hint @ \Store snapshot
		 * ```
		 */
		store_link_hint() {
			return this.$.$mol_locale.text( '$hyoo_life_store_link_hint' )
		}
		
		/**
		 * ```tree
		 * Stored $mol_icon_stored
		 * ```
		 */
		@ $mol_mem
		Stored() {
			const obj = new this.$.$mol_icon_stored()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Store_link $mol_link
		 * 	uri <= store_link?val
		 * 	hint <= store_link_hint
		 * 	sub / <= Stored
		 * ```
		 */
		@ $mol_mem
		Store_link() {
			const obj = new this.$.$mol_link()
			
			obj.uri = () => this.store_link()
			obj.hint = () => this.store_link_hint()
			obj.sub = () => [
				this.Stored()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * speed?val 0
		 * ```
		 */
		@ $mol_mem
		speed(val?: any) {
			if ( val !== undefined ) return val as never
			return 0
		}
		
		/**
		 * ```tree
		 * time_slowest_label @ \Slowest
		 * ```
		 */
		time_slowest_label() {
			return this.$.$mol_locale.text( '$hyoo_life_time_slowest_label' )
		}
		
		/**
		 * ```tree
		 * time_slow_label @ \Slow
		 * ```
		 */
		time_slow_label() {
			return this.$.$mol_locale.text( '$hyoo_life_time_slow_label' )
		}
		
		/**
		 * ```tree
		 * time_fast_label @ \Fast
		 * ```
		 */
		time_fast_label() {
			return this.$.$mol_locale.text( '$hyoo_life_time_fast_label' )
		}
		
		/**
		 * ```tree
		 * time_fastest_label @ \Fastest
		 * ```
		 */
		time_fastest_label() {
			return this.$.$mol_locale.text( '$hyoo_life_time_fastest_label' )
		}
		
		/**
		 * ```tree
		 * Time $mol_switch
		 * 	value?val <=> speed?val
		 * 	options *
		 * 		1 <= time_slowest_label
		 * 		5 <= time_slow_label
		 * 		25 <= time_fast_label
		 * 		1000 <= time_fastest_label
		 * ```
		 */
		@ $mol_mem
		Time() {
			const obj = new this.$.$mol_switch()
			
			obj.value = (val?: any) => this.speed(val)
			obj.options = () => ({
				1: this.time_slowest_label(),
				5: this.time_slow_label(),
				25: this.time_fast_label(),
				1000: this.time_fastest_label()
			})
			
			return obj
		}
		
		/**
		 * ```tree
		 * snapshot \0~-2ffff~10002~-20000~-1fffe~3~10003~20003
		 * ```
		 */
		snapshot() {
			return "0~-2ffff~10002~-20000~-1fffe~3~10003~20003"
		}
		
		/**
		 * ```tree
		 * snapshot_current
		 * ```
		 */
		snapshot_current() {
			return this.Map().snapshot_current()
		}
		
		/**
		 * ```tree
		 * population
		 * ```
		 */
		population() {
			return this.Map().population()
		}
		
		/**
		 * ```tree
		 * Map $hyoo_life_map
		 * 	speed <= speed
		 * 	snapshot <= snapshot
		 * 	snapshot_current => snapshot_current
		 * 	population => population
		 * ```
		 */
		@ $mol_mem
		Map() {
			const obj = new this.$.$hyoo_life_map()
			
			obj.speed = () => this.speed()
			obj.snapshot = () => this.snapshot()
			
			return obj
		}
	}
	
}

